<template>
  <div class="headTop">
    <div class="logo-div">
      <img src="../../assets/img/logo.png" @click="$router.push({path:'/employhomePage'})" />
    </div>
    <div class="baner-title">
      <template v-if="baner.length > 0">
        <span v-for="(item,index) in baner" :key="index" @click="goto(item.key)">{{item.title}}</span>
      </template>
    </div>
    <div class="userInfo">
      <el-dropdown trigger="click" class="md-userinfo-drop" @command="selectCommand">
        <span class="el-dropdown-link">{{userInfo.userName}}</span>
        <div class="head_img">
          <img
            :src="userInfo.avatar"
          />
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="management">个人管理</el-dropdown-item>
          <el-dropdown-item command="logout">退出登陆</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      baner: [
        {
          title: "优秀就业代表",
          key: "representModel"
        },
        {
          title: "招聘信息",
          key: "recruitmentModel"
        }
      ],
      userInfo: {},
      baseUrl:"http://localhost:8888"
    };
  },
  methods: {
    // goHomePage(){

    // },
    goto(val) {
      if (val == "recruitmentModel") {
        this.$router.push({ path: "/recruitmentModel" });
      } else {
        this.$router.push({ path: "/representModel" });
      }

      // console.log(val);
    },
    selectCommand(command) {
      if (command == "management" && this.userInfo.userType == 1) {
        window.location.href = `${process.env.BASE_URL}employment.html#/studentInfo`;
      } else if (command == "management" && this.userInfo.userType == 2) {
        window.location.href = `${process.env.BASE_URL}employment.html#/myInfo`;
      } else {
        sessionStorage.clear(); //清除sessionStorage
        window.location.href = `${process.env.BASE_URL}entering.html#/login`;
      }
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    // this.userInfo.avatar = window.URL.createObjectURL(this.userInfo.avatar)
    // console.log(this.userInfo.avatar)
  }
};
</script>
